<template>
  <div class="wiki-article">
      <articleHeader></articleHeader>
      <h1>title</h1>
    <div 
        v-for="(item,index) in mdList" 
        :key="item.type_id" 
        class="wiki-article-md look-md"
        ref="div"
        :title="item.subject"
        >
      <h2>{{item.subject}} <a href="javascript:;" @click="toggleModify(item.type_id,index)"></a></h2>
      <el-button type="success" plain v-show="item.content ? false : true" @click="handleAdd(item.type_id,index)"></el-button>
      <md 
        :value="item.content"
        v-model="content[index].content"
        ></md>
        <el-button type="primary" plain @click="subModify(item.type_id,index)"></el-button>
        <el-button type="danger" plain @click="closeModify(index)"></el-button>
    </div>
  </div>
</template>

<script>
import articleHeader from '@/components/publicHeader'
import md from "@/components/MarkdownEditor";
import * as ajax from "@/api";

export default {
  components: {
    md,
    articleHeader
  },
  data() {
    return {
      mdList: [],
      content:[],
      title:'',
    };
  },
  created() {
      console.log(this.$route)
    ajax.getArticleDetail(this.$route.query.row.id).then(resp => {
      if (resp.data.code) {
        console.log(resp)
        this.mdList = resp.data.data.article_info.content;
        this.content = [...resp.data.data.article_info.content]
      }
    });
    this.title = this.$route.query.row.title
  },
  methods:{
      toggleModify(type_id,index){
          ajax.authorization(this.$route.query.row.id,type_id).then(resp=>{
              if(resp.data.code){
                  if(resp.data.data.result){
                      this.$refs.div[index].classList.remove('look-md')
                  }
              }else{
                      alert('')
                }
          })
      },
      closeModify(index){
          if(confirm('')){
              this.$refs.div[index].classList.add('look-md')
              document.querySelectorAll('.el-button--success')[index].style.display = "block"
          }
      },
      subModify(type_id,index){
          ajax.modifyTechnicalPoints(this.$route.query.row.id,type_id,this.content[index].content).then(resp=>{
              if(resp.data.code){
                  alert(',')
                  this.$refs.div[index].classList.add('look-md')
                  document.querySelectorAll('.el-button--success')[index].style.display = "block"
              }
          })
      },
      handleAdd(id,index){
        this.$refs.div[index].classList.remove('look-md')
        console.log(document.querySelectorAll('.el-button--success')[index])
        document.querySelectorAll('.el-button--success')[index].style.display = "none"
      }
  }
};
</script>

<style lang='scss'>
.wiki-article {
  width: 100%;
  h1{
      margin-top: 20px;
      text-align: center;
      color: #808080;
  }
  h2{
      a{
          color: rgb(60, 148, 199);
          font-size: 15px;
          margin-left: 20px;
      }
  }
  &-md {
    width: 1280px;
    margin: 0 auto;
    margin-top: 50px;
    border-bottom: 1px solid #EBEEF5;
    .CodeMirror-scroll{
        padding-bottom: 170px;
    }
    .el-button--primary{
        float: right;
        display: inline-block;
        margin-top: 10px;
    }
    .el-button--danger{
        float: right;
        display: inline-block;
        margin-right: 20px;
        margin-top: 10px;
    }
    .el-button--success{
      margin-top: 10px;
    }
  }
  .look-md {
    .tui-editor-defaultUI{
        border: none;
    }
    .te-toolbar-section {
      display: none;
    }
    .te-editor {
      display: none;
    }
    .te-md-splitter {
      border: none;
    }
    .te-mode-switch-section{
        display: none !important;
    }
    .el-button--primary{
        display: none;
    }
    .el-button--danger{
        display: none;
    }
  }
}
</style>

